  line-height: 22px;
}

.page-header {
  background-color: white;
  padding-top: 73px;
  position: relative;
}

.page-header__logo {
  display: block;
  position: absolute;
  left: 27px;
  top: 26px;
}

.page-header__svg {
  fill: #192144;
}

.page-header__svg {
  display: none;
}

.page-header__svg--m {
  display: block;
}

