.item-country {
  width: 35px;
  height: 24px;
  position: relative;
  margin: 0px 0 18px 13px;
  cursor: pointer;
}


.item-country__name {
  display: block;
  padding: 10px;
  background: #101d41;
  font-family: "Bloggersans", Arial, sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: #ffffff;
  position: absolute;
  top: 100%;
  left: 0;
  border-radius: 4px;
  margin: 4px 0px 0 -25px;
}

.item-country:hover .item-country__name {
  // display: block;
}

.item-country::before {
  content: "";
  position: absolute;
  width: 35px;
  height: 24px;
  background-size: cover;
}

.item-country--sri-lanka::before {
  background-image: url("../img/srilanka.png");
  background-repeat: no-repeat;
}

.item-country--thailand::before {
  background-image: url("../img/Thailand.png");
  background-repeat: no-repeat;
}

.item-country--seychell::before {
  background-image: url("../img/Seychelles.png");
  background-repeat: no-repeat;
}

.item-country--belgium::before {
  background-image: url("../img/Belgium.png");
  background-repeat: no-repeat;
}

.item-country--czechia::before {
  background-image: url("../img/Czechia.png");
  background-repeat: no-repeat;
}
